function respawnPlayerInput() {
    // Ajouter un écouteur d'événements pour la touche "espace"
    document.addEventListener('keydown', function(event) {
        if (event.key === 'Shift') {
            // Code à exécuter lorsque la touche "espace" est enfoncée
            // Par exemple :
            console.log('La touche "espace" a été enfoncée');
            let ball1 = scene.getMeshByName("player1");
            player1.disableThisObject();
            ball1.dispose();
            player1 = new PlayerLevel3(scene,engine,"player1",'q','d','s',' ',0,8,-8);
            
        //document.getElementById("pin1").innerText = countPlayer1+"/93 PINS";
        
        // Modifier le texte de l'élément avec l'identifiant "pin2"
  
        }
    });

    // Ajouter un écouteur d'événements pour la touche "entrée"
    document.addEventListener('keydown', function(event) {
        if (event.code === 'Backspace') {
            // Code à exécuter lorsque la touche "entrée" est enfoncée
            // Par exemple :
            console.log('La touche "entrée" a été enfoncée');
            let ball2 = scene.getMeshByName("player2");
            player2.disableThisObject();
            ball2.dispose();
            player2 = new PlayerLevel3(scene,engine,"player2",'ArrowLeft','ArrowRight','i','Enter',25,8,-8);
            //document.getElementById("pin2").innerText = countPlayer2+"/93 PINS";
       
        }
    });

}


function displayPinNumber(){
    
     advancedTexture = BABYLON.GUI.AdvancedDynamicTexture.CreateFullscreenUI("UI");

    var button1 = BABYLON.GUI.Button.CreateSimpleButton("but1", "Click Me");
    button1.width = "1000px"
    button1.height = "40px";
    button1.color = "white";
    button1.cornerRadius = 20;
    button1.background = "green";
    button1.onPointerUpObservable.add(function() {
        alert("you did it!");
    });
    advancedTexture.addControl(button1);   
    
    

}

function loadNextLevel(){
    
    sceneManager.launchEnd();

}
function killLevel(){
    //scene.dispose();
     
    scene.meshes.forEach(function(mesh) {
        mesh.dispose();
    });
   
    scene.cameras.forEach(function(mesh) {
        mesh.dispose();
    });
    // Supprimer toutes les lumières de la scène
    scene.lights.forEach(function(light) {
        light.dispose();
    });

    
    engine.stopRenderLoop();
    hideControlUI();
    soundManager.stopMusic();
}

function displayControlUI(){
  
    // Récupération de l'élément par son ID
    var level1 = document.getElementById("level3");
 
    // Afficher l'élément
    level1.style.display = "block";
 
 }
 function hideControlUI(){
        // Récupération de l'élément par son ID
    var level1 = document.getElementById("level3");
 
    // Afficher l'élément
    level1.style.display = "none";
 
 }

export { name, scene, sceneData, launch };
